#AccueilCAS-h3{
    font-family: "Poiret One", serif;
    font-weight: lighter;
    margin: 10px 20px;
    color: #13435b;
}

.AccueilCAS-p{
    font-family: "Roboto", sans-serif;
    font-weight: normal;
    margin: 10px 0;
    text-indent: 30px;
    text-align: justify;
}

#AccueilCAS-titre{
    display: flex;
    align-items: center;
}

#AccueilCAS-CAS{
    margin-top: 20px;
    width: 100%;
}

@media only screen and (min-width: 768px) {
    #AccueilCAS-fleche{height: 200px;}
    #AccueilCAS-titre{margin-left: -100px;}
    #AccueilCAS-texte{margin: 100px;}
    .AccueilCAS-p{font-size: 20px;}
    #AccueilCAS-h3{font-size: 70px;}
}

@media only screen and (max-width: 767px) {
    #AccueilCAS-fleche{height: 75px;}
    #AccueilCAS-titre{margin-left: -20px;}
    #AccueilCAS-texte{margin: 20px;}
    .AccueilCAS-p{font-size: 18px;}
    #AccueilCAS-h3{font-size: 30px;}
}