#AccueilTeam-h3{
    font-family: "Poiret One", serif;
    font-weight: lighter;
    margin: 10px 20px;
    color: #13435b;
}

.AccueilTeam-p{
    font-family: "Roboto", sans-serif;
    font-weight: normal;
    margin: 10px 0;
    text-indent: 30px;
    text-align: justify;
}

#AccueilTeam-titre{
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

#AccueilCAS-CAS{
    margin-top: 20px;
    width: 100%;
}

#AccueilTeam-fleche{
    transform: rotate(180deg);
}

@media only screen and (min-width: 768px) {
    #AccueilTeam-fleche{height: 200px;}
    #AccueilTeam-titre{margin-right: -100px;}
    #AccueilTeam-texte{margin: 100px;}
    .AccueilTeam-p{font-size: 20px;}
    #AccueilTeam-h3{font-size: 70px;}
}

@media only screen and (max-width: 767px) {
    #AccueilTeam-fleche{height: 75px;}
    #AccueilTeam-titre{margin-right: -20px;}
    #AccueilTeam-texte{margin: 20px;}
    .AccueilTeam-p{font-size: 18px;}
    #AccueilTeam-h3{font-size: 30px;}
}