#AboutUs-h3{
    font-family: "Poiret One", serif;
    font-weight: lighter;
    margin: 10px 20px;
    color: #13435b;
}

.AboutUs-p{
    font-family: "Roboto", sans-serif;
    font-weight: normal;
    margin: 10px 0;
    text-indent: 30px;
    text-align: justify;
}

#AboutUs-titre{
    display: flex;
    align-items: center;
}

@media only screen and (min-width: 768px) {
    #AboutUs-fleche{height: 200px;}
    #AboutUs-titre{margin-left: -100px;}
    #AboutUs-texte{margin: 100px;}
    .AboutUs-p{font-size: 20px;}
    #AboutUs-h3{font-size: 70px;}
}

@media only screen and (max-width: 767px) {
    #AboutUs-fleche{height: 75px;}
    #AboutUs-titre{margin-left: -20px;}
    #AboutUs-texte{margin: 20px;}
    .AboutUs-p{font-size: 18px;}
    #AboutUs-h3{font-size: 30px;}
}